.RangeSlider-range {
  -webkit-appearance: none;
  background: -webkit-gradient(linear,
    left top, right top,
    from(#cc181e), color-stop(50%, #cc181e),
    color-stop(50%, #777), color-stop(62.5%, #777),
    color-stop(62.5%, #444), to(#444));
  background: -webkit-linear-gradient(left,
    #cc181e 0%, #cc181e 50%,
    #777 50%, #777 62.5%,
    #444 62.5%, #444 100%);
  background: -o-linear-gradient(left,
    #cc181e 0%, #cc181e 50%,
    #777 50%, #777 62.5%,
    #444 62.5%, #444 100%);
  background: linear-gradient(to right,
    #cc181e 0%, #cc181e 50%,
    #777 50%, #777 62.5%,
    #444 62.5%, #444 100%);
  cursor: pointer;
  height: 2px;
  width: 100%;
}

.RangeSlider-range::-webkit-slider-thumb {
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
  -webkit-appearance: none;
          appearance: none;
  background: #568af2;
  border-radius: 100%;
  height: 12px;
  width: 12px;
}

.RangeSlider-range::-webkit-slider-thumb:active {
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
  height: 18px;
  width: 18px;
}



