.Reconstruction {
  -ms-flex: 3 1;
      flex: 3 1;
  margin: 50px 50px 0 50px;

  display: grid;
  grid-template: repeat(16, 1fr) / repeat(1, 1fr);
  grid-gap: 37px;
  margin-top: 68px;
  margin-bottom: 18px;
}

.Reconstruction-container {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-top: 22px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Reconstruction-right {
  -ms-flex: 2 1;
      flex: 2 1;
  margin: 50px 50px 0 50px;
  max-width: 660px;
}

.Reconstruction-thumb {
  margin-bottom: 10px;
  border-radius: 2px;
}



